



































import { Component, Vue } from 'vue-property-decorator';
@Component({
  components: {},
})
export default class ViewComponent extends Vue {
  /**data */
  model = {
    amount: '',
  }
  fields = [
    {
      type: "input",
      modelKey: "amount",
      label: "",
      props: {
        placeholder: '请输入出借金额，100元起投，100元递增',
        clearable: true,
      },
      rules: {
        required: true,
      },
      messages: {
        required: "不能为空",
      },
    },
  ]
  options = {
    scrollToInvalidField: true,
    layout: 'classic' // standard classic
  }
  /**method */
  async submitHandler(e:any){
    e.preventDefault();
    if(this.$store.state.token){ // 判断是否登录
      // 判断是否金额充足
      const data = await this.$request({
        api:'appAccountStatus',
        params:{
          token: this.$store.state.token,
        }
      })
      if(data.openAccountStatus === 'SUCCESS'){ // 判断是否开户
        const data = await this.$request({
          api:'appAccount',
          params:{
            token: this.$store.state.token,
          }
        })
        if(data.resCode === '0000'){
          if(data.availableAmt >= 100){
            this.$router.replace(`/project/order/${this.$route.params.id}?amount=${this.model.amount}`)
          }else{
            this.$createDialog({ // 跳转开户绑卡页
              title: '可用余额不足',
              confirmBtn: '去充值',
              onConfirm: (e:any, promptValue:any) => {
                this.$router.replace('/account/lmCharge')
              }
            }).show()
          }
        }
      }else{
        this.$createToast({
          txt:'您尚未完成开户,去开户',
          time:3000,
          type:'txt',
          mask:true,
          onTimeout: () => {
            this.$router.replace('/account/lmAccount#open')
          },
        }).show()
      }
    }else{
      this.$router.replace('/user/login')
    }
  }
}
